$accent: #c77dde;

$c-text: #d1d1d1;
$c-back: #27233c;

// event based
// chat alert colors
$c-snag: #d55ec5;
$c-join: #50b671;
$c-left: #dc6464;

// action: mod removed
// recent: recently played text
$action: #dc6464;
$recent: #dc6464;

// hue shift amount
// on gold elements
$tinted: 170deg;

// generate our variables
@import "../import";

// define filters
// to apply color shifts
// to images / elements
// ( off - opacity: 1 )
.ts-vote-filter {
	@include shift($tinted, 2, 0.7); }

.ts-room-filter {
	@include gloss($tinted + 50deg, 4); }

.ts-main-filter {
	@include paint($tinted + 70deg, 2.7, 0.5); }

.ts-hard-filter {
	@include paint($tinted + 70deg, 2.7, 0.45, 1); }

.ts-icon-filter {
	@include paint($tinted + 70deg, 1, 1, 1, 0.5); }

.ts-mute-filter {
	@include paint($tinted + 40deg, 1.4, 0.4); }

.ts-fade-filter {
	opacity: 0.5; }

// build out the theme
@import "engine/import";
